var isNode = require("is-node");

var jsonp = function jsonp() {
  return console.warn("WARNING, YOU CALLED JSONP IN A NON-BROWSER CONTEXT");
};

if (!isNode) {
  jsonp = require("browser-jsonp");
}

var key;
var BASE_URL = "http://api.petfinder.com";
var ANIMALS = ["dog", "cat", "bird", "barnyard", "reptile", "smallfurry", "horse", "pig"];

var serialize = function serialize(res) {
  var acc = {};

  for (var prop in res) {
    if (!prop) {
      break;
    }

    if (res.hasOwnProperty(prop) && prop.charAt(0) !== "@") {
      if (res[prop].hasOwnProperty("$t")) {
        acc[prop] = res[prop]["$t"];
      } else if (Array.isArray(res[prop])) {
        acc[prop] = res[prop].map(function (item) {
          if (item.hasOwnProperty("$t")) {
            if (Object.getOwnPropertyNames(item).length > 1) {
              item.value = item["$t"];
              delete item["$t"];
              return item;
            } else {
              return item["$t"];
            }
          } else {
            return serialize(item);
          }
        });
      } else if (Object.getOwnPropertyNames(res[prop]).length === 0) {
        acc[prop] = null;
      } else {
        var serialized = serialize(res[prop]);
        acc[prop] = serialized;
      }
    }
  }

  return acc;
};

var request = function request(method, opts) {
  var newOpts = {
    key: key,
    format: "json"
  };
  newOpts = Object.assign(newOpts, opts);
  return new Promise(function (resolve, reject) {
    jsonp({
      url: BASE_URL + "/" + method,
      data: newOpts,
      success: function success(data) {
        resolve(serialize(data));
      },
      error: function error(err) {
        reject(err);
      }
    });
  });
};

var api = {
  breed: {
    list: function list(opts) {
      return request("breed.list", opts);
    }
  },
  pet: {
    getRandom: function getRandom(opts) {
      return request("pet.getRandom", opts);
    },
    get: function get(opts) {
      return request("pet.get", opts);
    },
    find: function find(opts) {
      return request("pet.find", opts);
    }
  },
  shelter: {
    getPets: function getPets(opts) {
      return request("shelter.getPets", opts);
    },
    listByBreed: function listByBreed(opts) {
      return request("shelter.listByBreed", opts);
    },
    find: function find(opts) {
      return request("shelter.find", opts);
    },
    get: function get(opts) {
      return request("shelter.get", opts);
    }
  }
};

module.exports = function createPetfinderSingleton(creds) {
  if (creds) {
    key = creds.key;
  }

  return api;
};

module.exports.ANIMALS = ANIMALS;